.avatar-container{display:flex;flex-direction:row;padding:0;margin:0}.avatar-container .avatar-wrapper{position:relative;height:250px;width:250px;margin:50px;overflow:hidden;box-shadow:1px 1px 15px -5px #000;transition:all .3s ease}.avatar-container .avatar-wrapper:hover{transform:scale(1.05);cursor:pointer}.avatar-container .avatar-wrapper:hover .profile-pic{opacity:.2}.avatar-container .avatar-wrapper .profile-pic{height:100%;width:100%;transition:all .3s ease}.avatar-container .avatar-wrapper .profile-pic:after{font-family:FontAwesome;content:"ÔÄá";top:0;left:0;width:100%;height:100%;position:absolute;font-size:190px;background:#ecf0f1;color:#34495e;text-align:center}.avatar-container .avatar-wrapper .upload-button{position:absolute;top:0;left:0;height:100%;width:100%}.avatar-container .avatar-wrapper .upload-button .fa-arrow-circle-up{position:absolute;height:100%;width:100%;font-size:234px;top:-17px;left:0;text-align:center;opacity:0;transition:all .3s ease;color:#34495e}.avatar-container .avatar-wrapper .upload-button:hover .fa-arrow-circle-up{opacity:1}.shop-info{margin-top:50px;width:100%}.shop-info .shop-info-name{text-align:center;font-size:20px;font-weight:500}.shop-info .shop-info__body{display:inline}.shop-info .shop-info__body .shop-info__title{font-size:.9rem;font-weight:700}